import React from 'react'
import { useNavigate } from 'react-router-dom'
// 
import { Carousel, Flex, Grid, WingBlank } from 'antd-mobile'
//  axios
import axios from 'axios'
//  utils 
import { getCurrentCity } from '../../utils'
//  BASE_URL
// import { BASE_URL } from '../../utils/url'
// 
import Nav1 from '../../assets/images/nav-1.png'
import Nav2 from '../../assets/images/nav-2.png'
import Nav3 from '../../assets/images/nav-3.png'
import Nav4 from '../../assets/images/nav-4.png'
// 
import './index.css'
// 
import SearchHeader from '../../components/SearchHeader'

// 
const navs = [
  {
    id: 1,
    img: Nav1,
    title: '',
    path: '/home/list',
  },
  {
    id: 2,
    img: Nav2,
    title: '',
    path: '/home/list',
  },
  {
    id: 3,
    img: Nav3,
    title: '',
    path: '/map',
  },
  {
    id: 4,
    img: Nav4,
    title: '',
    path: '/rent/add',
  },
]

// 
navigator.geolocation.getCurrentPosition((position) => {})

// 
// 1. 
// 2. ,
// :,
// ???
// 1.  state 
// 2. , true
// 3. 

class Index extends React.Component {
  state = {
    // 
    swipers: [],
    isSwiperLoaded: false,
    // 
    groups: [],
    // 
    news: [],
    // 
    curCityName: '',
  }
  // 
  async getSwipers() {
    const res = await axios.get('http://localhost:8080/home/swiper')
    this.setState(() => {
      return {
        swipers: res.data.body,
        isSwiperLoaded: true,
      }
    })
  }
  // 
  renderSwipers() {
    return this.state.swipers.map((item) => (
      <a
        key={item.id}
        href="http://itcast.cn"
        style={{
          display: 'inline-block',
          width: '100%',
          height: 212,
        }}
      >
        <img
          src={`http://localhost:8080${item.imgSrc}`}
          alt=""
          style={{ width: '100%', height: 212, verticalAlign: 'top' }}
        />
      </a>
    ))
  }
  // 
  renderNavs() {
    const { navigate } = this.props
    return navs.map((item) => (
      <Flex.Item key={item.id} onClick={() => navigate(item.path)}>
        <img src={item.img} alt="" />
        <h2>{item.title}</h2>
      </Flex.Item>
    ))
  }
  // 
  async getGroups() {
    const res = await axios.get('http://localhost:8080/home/groups', {
      params: { area: 'AREA%7C88cff55c-aaa4-e2e0' },
    })
    this.setState(() => {
      return {
        groups: res.data.body,
      }
    })
  }
  // 
  async getNews() {
    const res = await axios.get('http://localhost:8080/home/news', {
      params: { area: 'AREA%7C88cff55c-aaa4-e2e0' },
    })
    this.setState(() => {
      return {
        news: res.data.body,
      }
    })
  }
  // 
  renderNews() {
    return this.state.news.map((item) => (
      <div className="news-item" key={item.id}>
        <div className="imgwrap">
          <img className="img" src={`http://localhost:8080${item.imgSrc}`} alt="" />
        </div>
        <Flex className="content" direction="column" justify="between">
          <h3 className="title">{item.title}</h3>
          <Flex className="info" justify="between">
            <span>{item.from}</span>
            <span>{item.date}</span>
          </Flex>
        </Flex>
      </div>
    ))
  }
  async componentDidMount() {
    this.getSwipers()
    this.getGroups()
    this.getNews()
    // 2.  IP 
    // const curCity = new window.BMap.LocalCity()
    // curCity.get(async (res) => {
    //   const result = await axios.get(`http://localhost:8080/area/info?name=${res.name}`)
    //   this.setState(() => {
    //     return {
    //       curCityName: result.data.body.label,
    //     }
    //   })
    // })
    const curCity = await getCurrentCity()
    this.setState(() => {
      return {
        curCityName: curCity.label,
      }
    })
  }
  render() {
    return (
      <div className="index">
        {/*  */}
        <div className="swiper">
          {this.state.isSwiperLoaded ? (
            <Carousel autoplay infinite>
              {this.renderSwipers()}
            </Carousel>
          ) : (
            ''
          )}
          {/*  */}
          <SearchHeader cityName={this.state.curCityName} />
        </div>
        {/*  */}
        <Flex className="nav">{this.renderNavs()}</Flex>
        {/*  */}
        <div className="group">
          <h3 className="title">
            <span className="more"></span>
          </h3>
          {/*  */}
          <Grid
            data={this.state.groups}
            columnNum={2}
            square={false}
            hasLine={false}
            renderItem={(item) => (
              <Flex className="group-item" justify="around" key={item.id}>
                <div className="desc">
                  <p className="title">{item.title}</p>
                  <span className="info">{item.desc}</span>
                </div>
                <img src={`http://localhost:8080${item.imgSrc}`} alt="" />
              </Flex>
            )}
          />
        </div>
        {/*  */}
        <div className="new-s">
          <h3 className="group-title"></h3>
          <WingBlank size="md">{this.renderNews()}</WingBlank>
        </div>
      </div>
    )
  }
}

export default function History(props) {
  const navigate = useNavigate()
  return <Index {...props} navigate={navigate} />
}
